html,
body {
  height: 100%;
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
  font-family: 'Nanum Gothic', 'Sans-serif';
  color: #222;
}

body {
  background-color: #f3f3f3;
}

h1,
h2,
h3,
h4,
h5,
p,
dl,
dt,
dd,
ul {
  margin: 0;
  padding: 0;
}

input,
select,
textarea {
  border: 0;
  padding: 0;
  margin: 0;
  background: transparent;
  resize: none;
}

li {
  list-style: none;
}
a {
  color: #222;
  text-decoration: none;
}

button {
  font-family: inherit;
  font-size: 90%;
  margin: 0;
  cursor: pointer;
}
